body {
  background-color: #f0f0f0;
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
}

p {
  margin: 10px 0;
}

a {
  color: #0074d9;
  text-decoration: none;
}

a:hover {
  text-decoration: underline;
}

.form {
  max-width: 400px;
  background-color: #fff;
  border-radius: 5px;
  margin: 0 auto;
  padding: 20px;
  box-shadow: 0 2px 4px rgba(0, 0, 0, .1);
}

label {
  margin-bottom: 10px;
  display: block;
}

input[type="number"] {
  width: 100%;
  border: 1px solid #ccc;
  border-radius: 4px;
  padding: 8px;
  font-size: 16px;
}

button[type="submit"] {
  width: 100%;
  color: #fff;
  cursor: pointer;
  background-color: #0074d9;
  border: none;
  border-radius: 4px;
  padding: 10px;
  font-size: 16px;
  transition: background-color .3s;
  display: block;
}

button[type="submit"]:hover {
  background-color: #0056b3;
}

